.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    color: white;
  }
  
  .header-logo a {
    font-size: 1.5rem;
    font-weight: bold;
    text-decoration: none;
  }
  
  .header-nav a {
    margin: 0 15px;
    text-decoration: none;
    font-size: 1rem;
    transition: color 0.3s ease;
  }
  
  .header-nav a:hover {
    color: #a5f3fc; /* Adjust hover color as needed */
  }
  
  .nav-node {
    z-index: 3;
  }